import React from "react";
import TemplateHero from "../TemplatePage/TemplateHero";
import InfoSection from "../TemplatePage/InfoSection";
import ImageGroup from "../TemplatePage/ImageGroup";

import "../TemplatePage/projectTemplate.css";

const ProjectPage = () => {
  const imageFolder = "credd";
  return (
    <div className="project-template">
      <TemplateHero
        imageFolder={imageFolder}
        title="Credd - dream tracking mobile app"
        imageName="credd"
      />
      <InfoSection
        title="Project Description"
        paragraphs={[
          ` From the moment our company was founded, we have helped our clients
            find exceptional solutions for their businesses, creating memorable
            brands and digital products. Our expertise grows with each year, and
            our accumulated experience empowers us to develop products exactly
            as they`,
        ]}
      />

      <ImageGroup
        columns={2}
        images={["credd", "credd"]}
        imageFolder={imageFolder}
      />

      <InfoSection
        title="Project objectives"
        paragraphs={[
          `We create, products, brands, apps & websites for companies all around the world.`,
          `To us, world-class digital products take more than intelligent code or creative design. It requires true partnership between the ideators and the innovators. Our collaborations are built on a bedrock of transparency, communication, expert project management, and attention to detail.`,
        ]}
        buttonText="Launch project"
        link="http://google.com"
      />

      <ImageGroup
        columns={1}
        images={["credd", "credd"]}
        imageFolder={imageFolder}
      />

      <InfoSection
        title="Clients feedback"
        paragraphs={[
          `“From the moment our company was founded, we have helped our clients find exceptional solutions for their businesses, creating memorable brands and digital products. Our expertise grows with each year, and our accumulated experience empowers us to develop products exactly as they”`,
        ]}
        name="Michael Johnson"
        position="CEO"
      />
    </div>
  );
};

export default ProjectPage;
